.form {
  height: 90px;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  margin: 10px 0 10px;
  align-items: center;
  justify-content: space-around;
  border: solid 0.1px rgb(230, 230, 230);
  border-radius: 5px;
}

.label {
  display: flex;
  align-items: center;
  position: relative;
  font-size: 14px;
  margin: 0 auto 10px auto;
}

.input {
  color: rgb(90, 90, 90);
  min-width: 240px;
  height: 24px;
  border-radius: 5px;
  border: 0.1px solid rgb(230, 230, 230);
}
.btn {
  display: block;
  /*margin: 0 auto 0 auto;*/
  width: 160px;
  height: 20px;
  font-size: 14px;
  color: rgb(90, 90, 90);
  border: 0.1px solid rgb(200, 200, 200);
  border-radius: 5px;
  transition: background-color 0.25s;
  outline: none;
}

.reset_btn {
  position: absolute;
  right: 0;
  margin: 0 2px 0 auto;
  height: 20px;
  font-size: 10px;
  color: rgb(120, 120, 120);
  border: none;
  background-color: rgb(245, 245, 245);
  border-radius: 5px;
  transition: background-color 0.25s;
}

.btn:hover,
.reset_btn:hover {
  background-color: rgb(230, 230, 230);
}
